export default {
    data() {
        return {
            name:'',
            coloe:'',
        }
    },
    onLoad(option) {
        var that = this
        var data = JSON.parse(option.app)
        var date = JSON.parse(option.sunt)
        var datr = JSON.parse(option.tilo)
        that.data = data
        that.date = date
        that.datr = datr
    },
    methods:{
        end:function (e) {
            var that = this
            this.name = e.detail.value
        },
        ennd:function(e){
            var that = this
            this.coloe = e.detail.value
        },
        end1:function () {
            var that = this
            if(this.coloe==""){
            	uni.showToast({
            		icon:'none',
            		title:'请输入手机号'
            	})
            	return false;
            }
            var myreg = /^(16[0-9]|14[0-9]|13[0-9]|15[0-9]|17[0-9]|18[0-9]|19[0-9])\d{8}$$/;	
            if(!myreg.test(this.coloe)){
            	 uni.showToast({
            	 	title:'请输入正确的手机号',
            		icon:'none',
            		mask:true,
            		duration:2000
            	 })
            	  return false
             }
            var name = that.name
            var cool = that.coloe
            var data = that.data
            var date = that.date
            var datr = that.datr
            uni.request({
                url:this.url + 'buildorders/',
                method:'POST',
                header:{
                    Authorization: 'JWT'+' '+this.global_.token
                },
                data:{
                    name:name,
                    machine_id_list:data,
                    mobile:cool,
                    sale_num:date,
                    sale_money:datr
                },
                success(res) {
                    if(res.statusCode == 302){
                       uni.showToast({
                           title:'买家信息审核中',
                           icon:'none'
                       })
                    }
                    if(res.statusCode == 204) {
                        uni.showToast({
                            title:'买家信息不存在',
                            icon:'none'
                        })
                    }else if(res.statusCode == 200){
                        var posf = JSON.stringify(res.data.data)
                        uni.navigateTo({
                            url:'../confirm/confirm?ront=' + posf
                        })
                    }else if (res.statusCode == 401){
                        uni.showToast({
                            title:'矿机不能卖给自己',
                            icon:'none'
                        })
                    }else if(res.statusCode == 400) {
                        uni.showToast({
                            title:'请重新出售',
                        })
                    }
                }
            })
        },
    }
}
